* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  margin: 0;
  padding: 0;
}  

.App {
  max-width: 100vw;
  min-height: 100vh;
  position: relative;
  overflow-x: hidden;
  color: #4E4E4E;
  font-family: 'Poppins', sans-serif;
  .bkg {
    position: absolute;
    z-index: -1;
    background: linear-gradient(
      rgba(0, 0, 0, 0.7), 
      rgba(0, 0, 0, 0.7)
    ),
    url("https://assets.nflxext.com/ffe/siteui/vlv3/e178a4e7-4f52-4661-b2ae-41efa25dca7c/920ff518-b8d4-4669-a652-7faa8b14f960/BR-en-20210222-popsignuptwoweeks-perspective_alpha_website_large.jpg");
    background-attachment: fixed;
    border: none;
    width: 100vw;
    height: 100%;
    margin: -2px -1px -0px -1px;
  }

  article {
    padding: 50px 0 0 0;

    h1, h2 {
      margin: 12px 0;
      padding: 0 30px;
    }
    

    h1 {
      font-size: 50px;
      font-weight: 700;
      text-align: center;
    }

    p {
      margin: 12px 0;
    }
  }

  section {
    padding: 0 10vw;

    .subtitle-border {
      padding: 0 !important;
      width: 100%;
      margin: 11px 0;
      display: flex;
      justify-content: center;
      span {
        border: 1px solid #D62952;
        width: 30px;
        position: absolute;
      }
    }

    div:first-of-type {
      text-align: center;
      padding: 30px 0 60px;
    }

    h2 {
      font-weight: 700;
      font-size: 30px;
    }

    h3 {
      color: #5555C6;
      text-transform: uppercase;
      font-weight: 400;
      letter-spacing: -1%;
    }
  }
}