.founder-card-section {
    padding: 0 !important;
    justify-content: center;
    width: 25vw;
    max-width: 360px;
    margin: 0 35px;
    figure {
        display: flex;
        justify-content: flex-start;
        img {
            border-radius: 20px;
        }
    }
    article {
        padding: 0 !important;
        position: relative;
        div:first-of-type{
            padding: 30px 0 50px;
        }
        h4, p{
            text-align: left;
        }
        h4 {
            font-size: 24px;
        }
        p {
            font-size: 18px;
            line-height: 24px;
            font-weight: 400;
            margin: 25px 0;
        }
        span {
            background-color: #54E8B2;
            border-radius: 13px;
            width: 54px;
            height: 54px;
            position: absolute;
            bottom: 0;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}